.app-container {
    display: flex;
    justify-content: center;
    margin-top: 100px;
  }
  
  .gallery-container {
    display: flex;
    flex-direction: column;
    width: 85%;
    margin-top: 34px;
    margin-bottom: 34px;
    max-width: 1140px;
  }
  
  .selected-image {
    border-radius: 18px;
  }
  
  .heading {
    color: #1e293b;
    font-family: 'Roboto';
    font-size: 24px;
    font-weight: 500;
    margin-top: 24px;
  }
  
  .description {
    color: #64748b;
    font-family: 'Roboto';
    font-weight: 500;
    font-size: 16px;
  }
  
  .thumbnails-list {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    padding: 0px;
  }
  