.app-container {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ffc2a0;
    min-height: 100vh;
    margin-top: 100px;
  }
  
  .simple-todos-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #ffffff;
    border-radius: 16px;
    width: 90%;
    padding: 24px;
    margin-top: 48px;
    margin-bottom: 48px;
    max-width: 1110px;
  }
  
  @media screen and (min-width: 768px) {
    .simple-todos-container {
      padding: 48px;
      margin-top: 96px;
      margin-bottom: 96px;
    }
  }
  
  .heading {
    color: #ff8542;
    font-family: 'Roboto';
    font-size: 24px;
    font-weight: 700;
    margin-top: 12px;
    margin-bottom: 16px;
  }
  
  @media screen and (min-width: 768px) {
    .heading {
      font-size: 48px;
      margin-top: 32px;
      margin-bottom: 48px;
    }
  }
  
  .todos-list {
    padding-left: 0px;
  }
  